:root{
    --primary-white: #fff;
    --primary-orange: #bf00ff;
    --primary-dark: rgba(255,255,255,0);
    --primary-red: #ef4035;
    --primary-hover-red: #f33501;
}

body{
    font-family: 'Yomogi', cursive;
}

.logo{
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 0.75rem;
    border: 2px solid var(--primary-orange);
}
 nav a.nav-link {
     font-size: .8rem;
     letter-spacing: 0.0375;
     cursor: pointing;
 }
 nav ul li {
     text-transform: uppercase;
     
 }
 .navbar-nav > li > a{
     color: var(--primary-white) !important;
     margin-right: 0.75 rem;
     margin-left: 0.75 rem;
     border-top: 0.1875rem solid var(#bf00ff);
 }
 .navbar-brand {
     max-width: 17.25rem;
     padding: 0 !important;
     height: 6rem !important;
 }

 @media(max-width: 768px){
     .navbar-brand{
         width: 7rem;
     }
 }
 .navbar-nav > li > a.active, .navbar-nav > li > a.active:focus{
     border-top: 0.1875rem solid var(--primary-orange);
 }
 .navbar-nav > li > a:hover {
     border-top: 0.1875rem solid var(--primary-orange);
 }
 .header-wrapper{
     position: relative;
     background: url(OIP.jpg) no-repeat;
     background-size: cover;
     background-position: center;
     height: 90vh;

 }
 .main-info{
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: white;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'Yomogi', cursive;
    z-index: 1;

 }

 .main-info h1{
     text-transform: uppercase;
 }
 .typed-text {
     font-size: 2rem;
     text-transform: uppercase;
     color: black;
 }

 .btn-main-offer {
     border: 1px solid var(--primary-dark);
     text-transform: uppercase;
     border-radius: 0;
     padding: 0.625rem 0.75rem 0.75rem 0.75rem;
     color: black;
     margin: 2rem;

 }
 
 

 .btn-main-offer:hover{
     text-decoration-color: #bf00ff;
     background-color: azure;
     color: var(--primary-orange);
     transition: .2s ease-in-out;
 }

 @media(max-width: 768px) {
     .main-info h1{
         font-size: 1.5rem !important;
     }
     .typed-text{
         font-size: 1.3rem !important;
     }
 }